/*
    fonts and colors
    colors derived from: https://primer.style/prism
*/
@layer theme {
    :root {
        color-scheme: dark light;
        --font: ui-monospace, 'Cascadia Code', 'Source Code Pro', Menlo, Consolas, 'DejaVu Sans Mono', monospace;
    }

    @media (prefers-color-scheme: dark) {
        :root {
            --bg: hsla(0, 0%, 0%, 1);
            --bg-1: hsla(0, 0%, 5%, 1);
            --bg-2: hsla(0, 0%, 10%, 1);
            --divider: hsla(212, 8%, 47%, 1);
            --translucent: hsla(0, 0%, 0%, .8);
            --color: hsla(210, 29%, 97%, 1);
            --color-1: hsla(210, 24%, 93%, 1);
            --color-2: hsla(210, 18%, 84%, 1);
            --blue: hsla(204, 100%, 75%, 1);
            --blue-1: hsla(208, 100%, 66%, 1);
            --blue-2: hsla(211, 100%, 56%, 1);
            --blue-bg: hsla(216, 83%, 23%, 1);
            --green: hsla(135, 62%, 65%, 1);
            --green-1: hsla(137, 50%, 53%, 1);
            --green-2: hsla(137, 57%, 41%, 1);
            --green-bg: hsla(142, 100%, 12%, 1);
            --yellow: hsla(46, 79%, 61%, 1);
            --yellow-1: hsla(44, 66%, 50%, 1);
            --yellow-2: hsla(42, 100%, 37%, 1);
            --yellow-bg: hsla(35, 100%, 15%, 1);
            --orange: hsla(27, 100%, 74%, 1);
            --orange-1: hsla(25, 96%, 63%, 1);
            --orange-2: hsla(24, 76%, 51%, 1);
            --orange-bg: hsla(22, 100%, 18%, 1);
            --red: hsla(2, 100%, 83%, 1);
            --red-1: hsla(360, 100%, 75%, 1);
            --red-2: hsla(359, 95%, 63%, 1);
            --red-bg: hsla(346, 100%, 20%, 1);
            --purple: hsla(267, 100%, 86%, 1);
            --purple-1: hsla(265, 100%, 80%, 1);
            --purple-2: hsla(261, 92%, 72%, 1);
            --purple-bg: hsla(261, 59%, 30%, 1);
            --pink: hsla(327, 100%, 84%, 1);
            --pink-1: hsla(326, 100%, 75%, 1);
            --pink-2: hsla(325, 76%, 63%, 1);
            --pink-bg: hsla(320, 67%, 23%, 1);
            --coral: hsla(12, 100%, 82%, 1);
            --coral-1: hsla(11, 97%, 72%, 1);
            --coral-2: hsla(11, 81%, 60%, 1);
            --coral-bg: hsla(7, 91%, 22%, 1);
        }
    }

    @media (prefers-color-scheme: light) {
        :root {
            --bg: hsla(0, 0%, 100%, 1);
            --bg-1: hsla(0, 0%, 98%, 1);
            --bg-2: hsla(0, 0%, 96%, 1);
            --divider: hsla(212, 9%, 59%, 1);
            --translucent: hsla(0, 0%, 100%, .8);
            --color: hsla(213, 13%, 16%, 1);
            --color-1: hsla(212, 12%, 22%, 1);
            --color-2: hsla(212, 11%, 29%, 1);
            --blue: hsla(212, 92%, 45%, 1);
            --blue-1: hsla(213, 94%, 35%, 1);
            --blue-2: hsla(214, 96%, 28%, 1);
            --blue-bg: hsla(203, 100%, 86%, 1);
            --green: hsla(137, 66%, 30%, 1);
            --green-1: hsla(138, 71%, 23%, 1);
            --green-2: hsla(141, 90%, 16%, 1);
            --green-bg: hsla(134, 66%, 80%, 1);
            --yellow: hsla(40, 100%, 30%, 1);
            --yellow-1: hsla(37, 100%, 25%, 1);
            --yellow-2: hsla(36, 98%, 20%, 1);
            --yellow-bg: hsla(48, 93%, 74%, 1);
            --orange: hsla(24, 100%, 37%, 1);
            --orange-1: hsla(23, 100%, 29%, 1);
            --orange-2: hsla(22, 100%, 23%, 1);
            --orange-bg: hsla(28, 100%, 85%, 1);
            --red: hsla(356, 72%, 47%, 1);
            --red-1: hsla(350, 84%, 35%, 1);
            --red-2: hsla(349, 90%, 27%, 1);
            --red-bg: hsla(3, 100%, 90%, 1);
            --purple: hsla(261, 69%, 59%, 1);
            --purple-1: hsla(261, 53%, 48%, 1);
            --purple-2: hsla(261, 56%, 38%, 1);
            --purple-bg: hsla(271, 100%, 92%, 1);
            --pink: hsla(324, 54%, 49%, 1);
            --pink-1: hsla(323, 59%, 38%, 1);
            --pink-2: hsla(322, 58%, 30%, 1);
            --pink-bg: hsla(327, 100%, 91%, 1);
            --coral: hsla(9, 64%, 47%, 1);
            --coral-1: hsla(9, 70%, 36%, 1);
            --coral-2: hsla(8, 79%, 28%, 1);
            --coral-bg: hsla(12, 100%, 90%, 1);
        }
    }

    body {
        font-family: var(--font);
        background-color: var(--bg);
        color: var(--color);
    }

    pre,
    code {
        color: var(--green);
        font-size: .9rem;
    }

    hr {
        border: none;
        height: 1px;
        background-color: var(--divider);
    }
}